/**
 * * ****************************** ACTIONS *****************************
 */
export type FT_GetDistributionDistrict = (
  academic_id: string,
  callBack: (
    status: boolean,
    data: API_DistributedDistricts | null,
    msg: string
  ) => void
) => void;
export const FC_GetDistributionDistrict = async (
  academic_id: string,
  callBack: (
    status: boolean,
    data: API_DistributedDistricts | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<API_DistributedDistricts>(
      `${url}/rebdistribution/academic/${academic_id}`
    );

    console.log({ red_distribution_data: res.data });

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};

export interface SaveDistributionDIstrictData {
  academic_year_id: string;
  minecofin_limit_id: string;
  district_code: string;
  qualification_id: string;
  limits: number;
  distribution_comment: string;
}

export type FT_SaveDistributionDistrict = (
  send: SaveDistributionDIstrictData,
  callBack: (
    status: boolean,
    data: API_RES_SaveDistributed | null,
    msg: string
  ) => void
) => void;
export const FC_SaveDistributionDistrict = async (
  send: SaveDistributionDIstrictData,
  callBack: (
    status: boolean,
    data: API_RES_SaveDistributed | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.post<API_RES_SaveDistributed>(
      `${url}/rebdistribution`,
      send
    );

    callBack(true, res.data, "");
  } catch (error) {
    callBack(false, null, errorToText(error));
  }
};

//* DDE
export type FT_GetDistributionSchools = (
  data_send: { academic_id: string; district_id: string },
  callBack: (
    status: boolean,
    data: API_DistributedDDE | null,
    msg: string
  ) => void
) => void;
export const FC_GetDistributionSchools = async (
  { academic_id, district_id }: { academic_id: string; district_id: string },

  callBack: (
    status: boolean,
    data: API_DistributedDDE | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<API_DistributedDDE>(
      `${url}/districtdistribution/peracademic/${district_id}/${academic_id}`
    );

    console.log({ distribution_data: res.data });

    callBack(true, res.data, "");
  } catch (error) {
    console.log(error);
    callBack(false, null, errorToText(error));
  }
};

