import axios from "axios";
import { APP_TOKEN_NAME } from "../config/app.config";

export const setAxiosToken = () => {
  // console.log("API-Request");
  if (localStorage[APP_TOKEN_NAME]) {
    // console.log({ header_jwt: localStorage[APP_TOKEN_NAME] });

    axios.defaults.headers.common["Tmis-Token"] = localStorage[APP_TOKEN_NAME];
  } else {
    delete axios.defaults.headers.common["Tmis-Token"];
  }
};
