import { Action, ActionTypes, SchoolHierarchyStore } from "../actions";

// default state
const defaultState: SchoolHierarchyStore = {
  hierarchy: null,
  selected_hierarchy: null,
};

export const schoolHierarchyReducer = (
  state: SchoolHierarchyStore = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.GET_ALL_HIERARCHY:
      return {
        ...state,
        hierarchy: action.payload,
      };
    default:
      return state;
  }
};
