/**
 * * ****************************** ACTIONS *****************************
 */
export interface AddBasicInfo {
  type: ActionTypes.ADD_BASIC_INFO;
  payload: Basic;
}

export type FT_AddBasicInfo = (
  callBack: (status: boolean, msg: string) => void
) => void;
export const FC_AddBasicInfo = (
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<Basic>(`${url}/basicinfos/acalqual`);
      // * save data
      dispatch<AddBasicInfo>({
        type: ActionTypes.ADD_BASIC_INFO,
        payload: res.data,
      });
      callBack(true, "");
    } catch (error) {
      callBack(
        false,
        errorToText(error, () => {})
      );
    }
  };
};

export interface GetSchoolsByDistrict {
  type: ActionTypes.LOAD_SCHOOLS_BY_DISTRICT;
  payload: SchoolData[];
}

export type FT_GetSchoolsByDistrictRedux = (
  district_id: string,
  callBack: (status: boolean, msg: string) => void
) => void;
// SchoolInterface
export const FC_GetSchoolsByDistrictRedux = (
  district_id: string,
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      // const res = await axios.get<SchoolInterface[]>(
      //   `${url}/basicinfos/acalqual`
      // );

      const res = await axios.get<SchoolData[]>(
        `${url}/report/district/schools/${district_id}`
      );

      // * save data
      dispatch<GetSchoolsByDistrict>({
        type: ActionTypes.LOAD_SCHOOLS_BY_DISTRICT,
        payload: res.data,
      });
      callBack(true, "");
    } catch (error) {
      callBack(
        false,
        errorToText(error, () => {})
      );
    }
  };
};

// * Post limit
export interface PostLimit {
  academic_year_id: string;
  qualification_id: string;
  limits: number;
}

export interface PostLimitState extends PostLimit {
  qualification_name: string;
}

export interface LimitResponce {
  minecofin_limit_id: string;
  academic_year_id: string;
  qualification_id: string;
  limits: string | number;
  created_by: string | null;
  created_date: Date | null;
  updated_by: string | null;
  updated_date: string | null;
  archive: string | null;
  archived_by: string | null;
  archived_date: Date | null;
}

export interface CurrentTeacher {
  qualification_id: string;
  total_teachers: string;
}
export interface GetPostLimit {
  limits: LimitResponce[];
  current_teacher: CurrentTeacher[];
}

export type FT_GetPosyLimit = (
  academic_id: string,
  callBack: (
    status: boolean,
    post_limits: GetPostLimit | null,
    msg: string
  ) => void
) => void;
export const FC_GetPosyLimit = (
  academic_id: string,
  callBack: (
    status: boolean,
    post_limits: GetPostLimit | null,
    msg: string
  ) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<GetPostLimit>(
        `${url}/minicofinlimit/academic/${academic_id}`
      );
      // * save data
      callBack(true, res.data, "");
    } catch (error) {
      callBack(false, null, errorToText(error));
    }
  };
};

export type FT_SavePostLimit = (
  data: PostLimit[],
  callBack: (status: boolean, msg: string) => void
) => void;
export const FC_SavePostLimit = (
  data: PostLimit[],
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.post<{ message: string }>(
        `${url}/minicofinlimit/create`,
        data
      );
      // * save data
      callBack(true, res.data.message);
    } catch (error) {
      callBack(false, errorToText(error));
    }
  };
};

