import axios from "axios";
import { Dispatch } from "redux";
import { APP_URL } from "../config/app.config";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { ActionTypes } from "./types";

export enum HierarchyDataType {
  SCHOOL_LEVELS = "SCHOOL_LEVELS",
  COMBINATIONS = "COMBINATIONS",
}

export interface ExtractTypeHierarchy {
  type: HierarchyDataType;
  status: boolean;
}

export interface FilteredLevel {
  level_code: string;
  level_name: string;
}
export interface FilteredCombination {
  combination_code: string;
  combination_name: string;
}

// action url
const url = APP_URL;

/**
 * * ****************************** INTERFACES *****************************
 */

