export const FC_GetSchoolLevels = (
  data: SchoolHierarchyInterface[],
  filter: ExtractTypeHierarchy
): { levels: FilteredLevel[]; combinations: FilteredCombination[] } => {
  let levels: FilteredLevel[] = [];
  let combinations: FilteredCombination[] = [];

  for (let element of data) {
    if (
      filter.status === true &&
      filter.type === HierarchyDataType.SCHOOL_LEVELS
    ) {
      if (
        levels.find((itm) => itm.level_code === element.level_code) ===
        undefined
      ) {
        levels.push({
          level_code: element.level_code,
          level_name: element.level_name,
        });
      }
    }
    if (
      filter.status === true &&
      filter.type === HierarchyDataType.COMBINATIONS
    ) {
      if (
        combinations.find(
          (itm) => itm.combination_code === element.combination_code
        ) === undefined
      ) {
        combinations.push({
          combination_code: element.combination_code,
          combination_name: element.combination_name,
        });
      }
    }
  }
  return { levels: levels, combinations: combinations };
};
