export interface TeacherDataRole {
  role_id: string;
  role: string;
  access: string;
  status: string;
}

export interface API_GetTeachersBySchool {
  staff: TeacherData[];
  role: TeacherDataRole[];
}

const url = APP_URL;

export type FT_GetTeachersBySchool = (
  school_id: string,
  callBack: (
    status: boolean,
    data: API_GetTeachersBySchool | null,
    msg: string
  ) => void
) => void;
export const FC_GetTeachersBySchool = async (
  school_id: string,
  callBack: (
    status: boolean,
    data: API_GetTeachersBySchool | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<API_GetTeachersBySchool>(
      `${url}/report/school/teachers/${school_id}`
    );
    // console.log(res);
    callBack(true, res.data, "");
  } catch (error) {
    console.log(error);
    callBack(false, null, errorToText(error));
  }
};

