//* Get school details counta
export const FC_RequestTeachersInTRS = async (
  position_code: string,
  data: RequestInTRS,
  callBack: (
    status: boolean,
    data: ReturnedCandidates[] | null,
    msg: string
  ) => void
) => {
  try {
    // setAxiosToken();
    const res = await axios.post<ReturnedCandidates[]>(
      TRS_REQUEST_CANDIDATES + `/${position_code}`,
      [data]
    );
    console.log({ sent_data: res.data });

    callBack(true, res.data, "");
  } catch (error) {
    console.log(error);
    callBack(false, null, errorToText(error));
  }
};

