import axios from "axios";
import React from "react";
import { Link, Redirect } from "react-router-dom";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import { APP_URL } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { errorToText } from "../../utils/errors";

// props for the component
interface AppProps {
  phone_number: string;
}

interface AppState {
  received_code: string;
  new_password: string;
  confirm_password: string;
  loading: boolean;
  success: string;
  error: {
    target:
      | "received_code"
      | "main"
      | "new_password"
      | "confirm_password"
      | null;
    msg: string;
  };
}

export class UpdatePassword extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      success: "",
      received_code: "",
      new_password: "",
      confirm_password: "",
      loading: false,
      error: {
        target: null,
        msg: "",
      },
    };
  }

  receiveCode = async (e: any) => {
    e.preventDefault();

    //* Validation
    if (this.state.received_code === "") {
      return this.setState({
        error: {
          target: "received_code",
          msg: "Please fill verification code",
        },
      });
    }
    if (this.state.new_password.length <= 4) {
      return this.setState({
        error: {
          target: "new_password",
          msg: "Password must not be lass than 4 character",
        },
      });
    }
    if (this.state.confirm_password === "") {
      return this.setState({
        error: {
          target: "confirm_password",
          msg: "Please confirm password",
        },
      });
    }
    if (this.state.new_password !== this.state.confirm_password) {
      return this.setState({
        error: {
          target: "confirm_password",
          msg: "Password does not match",
        },
      });
    }
    this.setState({
      loading: true,
    });
    try {
      const res = await axios.post(`${APP_URL}/changepassword/verify`, {
        phone_number: this.props.phone_number,
        verification_code: this.state.received_code,
        new_password: this.state.new_password,
      });
      if (res.status === 200) {
        this.setState({ success: "Password reset done successfully!" });
        console.log("Response: ", res.data);
      }
    } catch (error: any) {
      this.setState({ error: { target: "main", msg: errorToText(error) } });
      console.log("Error:", { ...error });
    }
    this.setState({
      loading: false,
    });
  };

  render() {
    // if (this.state.success !== "") {
    //   return <Redirect to="/login" />;
    // }
    return (
      <>
        {this.state.error.target === "main" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <div className="min-h-screen bg-gray-100 dark:bg-dark-900 flex items-center">
          <div className="bg-white py-10 rounded border mt-5  dark:bg-dark-800 w-full max-w-md mx-auto px-5">
            <form onSubmit={this.receiveCode}>
              <h1 className="text-center text-3xl mb-6 text-primary-800 dark:text-primary-dark  font-extrabold">
                TMIS | Reset password
              </h1>

              {this.state.success === "" && (
                <TextInput
                  error={
                    this.state.error.target === "received_code"
                      ? this.state.error.msg
                      : ""
                  }
                  label="Verification code"
                  name="received_code"
                  onChange={(e) =>
                    this.setState({
                      received_code: e.target.value,
                      error: {
                        target: null,
                        msg: "",
                      },
                    })
                  }
                  placeholder=""
                  value={this.state.received_code}
                  disabled={this.state.loading}
                  type="text"
                />
              )}
              {this.state.success === "" && (
                <TextInput
                  error={
                    this.state.error.target === "new_password"
                      ? this.state.error.msg
                      : ""
                  }
                  label="Create new password"
                  name="new_password"
                  onChange={(e) =>
                    this.setState({
                      new_password: e.target.value,
                      error: {
                        target: null,
                        msg: "",
                      },
                    })
                  }
                  placeholder=""
                  value={this.state.new_password}
                  disabled={this.state.loading}
                  type="password"
                />
              )}
              {this.state.success === "" && (
                <TextInput
                  error={
                    this.state.error.target === "confirm_password"
                      ? this.state.error.msg
                      : ""
                  }
                  label="Confirm password"
                  name="confirm_password"
                  onChange={(e) =>
                    this.setState({
                      confirm_password: e.target.value,
                      error: {
                        target: null,
                        msg: "",
                      },
                    })
                  }
                  placeholder=""
                  value={this.state.confirm_password}
                  disabled={this.state.loading}
                  type="password"
                />
              )}

              <div className="mt-5 flex items-center justify-center">
                {this.state.success !== "" ? (
                  <Link
                    to="/login"
                    className="bg-green-700 text-white cursor-pointer px-3 py-2 rounded text-center w-max"
                  >
                    Reset done, Click here to login
                  </Link>
                ) : (
                  <Button full loading={this.state.loading}>
                    Reset password
                  </Button>
                )}
              </div>

              <div className="mt-4 text-center">
                <Link to="/login" className=" cursor-pointer">
                  Cancel process
                </Link>
              </div>
            </form>
          </div>
        </div>
      </>
    );
  }
}
