import axios from "axios";
import { APP_URL } from "../config/app.config";
import { SchoolPositionsCombination } from "../containers/Head-Teacher/Request/HeadTeacherRequestToDee";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";

// action url
const url = APP_URL;

export interface SchoolData {
  school_id: string;
  school_code: string;
  school_name: string;
  school_category: string;
  school_status: string;
  region_code: string;
}

export type FT_GetSchoolsByDistrict = (
  district_id: string,
  callBack: (status: boolean, data: SchoolData[] | null, msg: string) => void
) => void;
export const FC_GetSchoolsByDistrict = async (
  district_id: string,
  callBack: (status: boolean, data: SchoolData[] | null, msg: string) => void
) => {
  try {
    setAxiosToken();
    const res = await axios.get<SchoolData[]>(
      `${url}/report/district/schools/${district_id}`
    );

    callBack(true, res.data, "");
  } catch (error) {
    console.log(error);
    callBack(false, null, errorToText(error));
  }
};

export const FC_GetSchoolPositions = async (
  school_code: string,
  academic_year_id: string,
  callBack: (
    status: boolean,
    data: SchoolPositionsCombination | null,
    msg: string
  ) => void
) => {
  setAxiosToken();
  try {
    const res = await axios.get<SchoolPositionsCombination>(
      // `${APP_URL}/schoolposition/schpos/${school_code}`
      `${APP_URL}/schoolpostrequest/request/${school_code}/${academic_year_id}`
    );
    callBack(true, res.data, "");
  } catch (error: any) {
    console.log("Err: ", { ...error });
    callBack(false, null, errorToText(error));
  }
};
