import { Action, ActionTypes } from "../actions";
import { APITerminationType } from "../types/StaffTermination";

export type TerminationReducerType = {
  allTermination: APITerminationType[] | null;
};
// default state
const defaultState: TerminationReducerType = { allTermination: null };

export const terminationReducer = (
  state: TerminationReducerType = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.GET_ALL_TERMINATION:
      return {
        ...state,
        allTermination: action.payload,
      } as TerminationReducerType;
    case ActionTypes.UPDATE_TERMINATION:
      return {
        ...state,
        allTermination: action.payload,
      } as TerminationReducerType;
    // case ActionTypes.UPDATE_TERMINATION:
    //   if (state.allTermination) {
    //     const index = state.allTermination.findIndex(
    //       (item) =>
    //         item.change_staff_status_id ===
    //         action.payload.change_staff_status_id
    //     );
    //     if (index >= 0) {
    //       let terminations = [...state.allTermination];
    //       terminations[index].decided_by_date = new Date().toDateString();
    //       terminations[index].decided_by_comment =
    //         action.payload.decided_by_comment;
    //       terminations[index].decided_to_suspend_to =
    //         action.payload.decided_to_suspend_to;
    //       terminations[index].decided_to_suspend_from =
    //         action.payload.decided_to_suspend_from;
    //       return {
    //         ...state,
    //         allTermination: terminations,
    //       } as TerminationReducerType;
    //     }
    //   }
    //   return state;
    default:
      return state;
  }
};
