import { Placement, Action, ActionTypes } from "../actions";

// default state
const defaultState = {
  loading: true,
  placements: [],
  academics: null,
} as Placement;

export const placementReducer = (
  state: Placement = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.SET_PLACEMENT_LIST:
      return {
        ...state,
        placements: action.payload,
        loading: false,
      } as Placement;
    case ActionTypes.UPDATE_PLACEMENT_DATA:
      return {
        ...state,
        placements: state.placements.map((itm) =>
          itm.placement_list_approval_id === action.payload.placement_id
            ? {
                ...itm,
                list: action.payload.list,
                ht_comment: action.payload.comment,
              }
            : itm
        ),
      } as Placement;
    case ActionTypes.CHANGE_PLACEMENT_STATUS:
      return {
        ...state,
        placements: state.placements.map((itm) =>
          itm.placement_list_approval_id === action.payload.id
            ? {
                ...itm,
                dde_approval_status: "1",
                dde_comment: action.payload.dde_comment,
              }
            : itm
        ),
      } as Placement;
    case ActionTypes.GET_ACADEMICS:
      return {
        ...state,
        academics: action.payload,
      } as Placement;
    default:
      return state;
  }
};
