import axios from "axios";
import React from "react";
import { Link } from "react-router-dom";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import { APP_URL } from "../../config/app.config";
import { StoreState } from "../../reducers";
import { errorToText } from "../../utils/errors";
import { UpdatePassword } from "./UpdatePassword";

interface RequestSuccessResponse {
  message: string;
  results: {
    cost: string;
    credits: string;
    message: string;
    messageid: string;
    receipient: string;
    sender: string;
    status: string;
  };
}

// props for the component
interface AppProps {}

interface AppState {
  username: string;
  loading: boolean;
  success: RequestSuccessResponse | null;
  error: {
    target: "username" | "main" | null;
    msg: string;
  };
}

export class ForgetPassword extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      success: null,
      username: "",
      loading: false,
      error: {
        target: null,
        msg: "",
      },
    };
  }

  receiveCode = async (e: any) => {
    e.preventDefault();

    //* Validation
    if (this.state.username.length <= 4) {
      return this.setState({
        error: {
          target: "username",
          msg: "Phone must not be lass than 4 character",
        },
      });
    }
    this.setState({
      loading: true,
    });
    try {
      const res = await axios.post<RequestSuccessResponse>(
        `${APP_URL}/changepassword/forget`,
        {
          phone_number: this.state.username,
        }
      );
      if (res.status === 200) {
        this.setState({ success: res.data });
        console.log("Response: ", res.data);
      }
    } catch (error: any) {
      this.setState({ error: { target: "main", msg: errorToText(error) } });
      console.log("Error:", { ...error });
    }
    this.setState({
      loading: false,
    });
  };

  render() {
    if (this.state.success !== null) {
      return <UpdatePassword phone_number={this.state.username} />;
    }
    // if (this.state.loading === true) {
    //   return <Loading />;
    // }
    return (
      <>
        {this.state.error.target === "main" && (
          <Alert theme="danger" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <div className="min-h-screen bg-gray-100 dark:bg-dark-900 flex items-center">
          <div className="bg-white py-10 rounded border mt-5  dark:bg-dark-800 w-full max-w-md mx-auto px-5">
            <form onSubmit={this.receiveCode}>
              <h1 className="text-center text-3xl mb-6 text-primary-800 dark:text-primary-dark  font-extrabold">
                TMIS | Password recovery
              </h1>
              <div className="bg-blue-100 text-primary-800 px-3 py-2 rounded">
                You receive verification code on your phone number
              </div>
              <TextInput
                error={
                  this.state.error.target === "username"
                    ? this.state.error.msg
                    : ""
                }
                label="Phone"
                name="phone"
                onChange={(e) =>
                  this.setState({
                    username: e.target.value,
                    error: {
                      target: null,
                      msg: "",
                    },
                  })
                }
                placeholder="25070000000"
                value={this.state.username}
                disabled={this.state.loading}
                type="number"
              />

              <div className="mt-5">
                <Button full loading={this.state.loading}>
                  Send code
                </Button>
              </div>

              <div className="mt-4 text-center">
                <Link to="/login" className=" cursor-pointer">
                  Login my account
                </Link>
              </div>
            </form>
          </div>
        </div>
      </>
    );
  }
}
