import { Basic, Action, ActionTypes } from "../actions";

// default state
const defaultState = {
  loading: true,
  academic_calender: null,
  qualifications: [],
  register_assets: {
    error: false,
    loading: true,
    // loading: false,
    data: null,
    // data: {
    //   banks: [],
    //   contract_types: [],
    //   countries: [],
    //   education_domains: [],
    //   education_sub_domains: [],
    //   marital: [],
    //   qualifications: [],
    //   specialization: [],
    //   staff_category: [],
    // },
  },
  assign_teacher_assets: {
    error: false,
    loading: true,
    data: null,
  },
  positions: null,
  roles: null,
} as Basic;

export const basicReducer = (state: Basic = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.ADD_BASIC_INFO:
      return { ...state, ...action.payload, loading: false } as Basic;
    case ActionTypes.REGISTER_STAFF_ASSETS:
      return {
        ...state,
        register_assets: {
          data: { ...action.payload },
          loading: false,
        },
      } as Basic;
    case ActionTypes.ASSIGN_STAFF_ASSETS:
      return {
        ...state,
        assign_teacher_assets: {
          data: { ...action.payload },
          loading: false,
        },
      } as Basic;
    case ActionTypes.GET_ALL_POSITIONS:
      return {
        ...state,
        positions: action.payload,
      } as Basic;
    case ActionTypes.GET_ALL_SYSTEM_ROLES:
      return {
        ...state,
        roles: action.payload,
      } as Basic;
    case ActionTypes.LOAD_SCHOOLS_BY_DISTRICT:
      return {
        ...state,
        assign_teacher_assets: {
          ...state.assign_teacher_assets,
          data:
            state.assign_teacher_assets.data === null
              ? null
              : {
                  ...state.assign_teacher_assets.data,
                  schools: action.payload,
                },
        },
      };
    default:
      return state;
  }
};
