import axios from "axios";
import { Dispatch } from "redux";
import { APP_URL } from "../config/app.config";
import { ReportAcademicYearType } from "../types/PlacementReportTypes";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { ActionTypes } from "./types";

export interface PlacementDataCreate {
  school_code: string;
  district_code: string;
  academic_year_id: string;
  list: string;
  month: string;
  ht_comment: string;
}

export interface Placement {
  loading: boolean;
  placements: API_GetPlacementBySchool[];
  academics: ReportAcademicYearType[] | null;
}

const url = APP_URL;

export type FT_SubmitPlacementList = (
  data_send: PlacementDataCreate,
  callBack: (
    status: boolean,
    data: { message: string } | null,
    msg: string
  ) => void
) => void;
export const FC_SubmitPlacementList = async (
  data_send: PlacementDataCreate,
  callBack: (
    status: boolean,
    data: { message: string } | null,
    msg: string
  ) => void
) => {
  try {
    setAxiosToken();
    let res = await axios.post<{ message: string }>(
      `${url}/placementapproval/save`,
      data_send
    );

    callBack(true, res.data, "");
  } catch (error) {
    console.log(error);
    callBack(false, null, errorToText(error));
  }
};

export interface API_GetPlacementBySchool {
  placement_list_approval_id: string;
  school_code: string;
  district_code: string;
  academic_year_id: string;
  head_teacher_id: string;
  dde_id: string | null;
  list: string;
  dde_approval_status: string;
  approved_date: Date | null;
  month: string;
  created_by: string;
  created_at: Date;
  updated_by: string | null;
  updated_at: Date | null;
  school_name: string;
  head_teacher_name: string;
  dde_name: string | null;
  district_name: string;
  ht_comment: string;
  dde_comment: string;
}

export interface GetPlacementBySchool {
  type: ActionTypes.SET_PLACEMENT_LIST;
  payload: API_GetPlacementBySchool[];
}
export type FT_GetPlacementBySchool = (
  { school_code, academic_id }: { school_code: string; academic_id: string },
  callBack: (status: boolean, msg: string) => void
) => void;

export const FC_GetPlacementBySchool = (
  { school_code, academic_id }: { school_code: string; academic_id: string },
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();

    try {
      const res = await axios.get<API_GetPlacementBySchool[]>(
        `${url}/placementapproval/slist/school/${school_code}/${academic_id}`
      );

      dispatch<GetPlacementBySchool>({
        type: ActionTypes.SET_PLACEMENT_LIST,
        payload: res.data,
      });

      callBack(true, "");
    } catch (error) {
      console.log({ error });
      callBack(false, errorToText(error));
    }
  };
};

