//* Update placement
export interface UpdatePlacement {
  placement_id: string;
  comment: string;
  list: string;
}

//** PLACEMENT UPDATE
export interface UpdatePlacementBySchool {
  type: ActionTypes.UPDATE_PLACEMENT_DATA;
  payload: UpdatePlacement;
}

export type FT_UpdatePlacementList = (
  placement_id: string,
  data_send: PlacementDataCreate,
  callBack: (status: boolean, msg: string) => void
) => void;

export const FC_UpdatePlacementList = (
  placement_id: string,
  data_send: PlacementDataCreate,
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();

    try {
      await axios.post(
        `${url}/placementapproval/update/${placement_id}`,
        data_send
      );

      dispatch<UpdatePlacementBySchool>({
        type: ActionTypes.UPDATE_PLACEMENT_DATA,
        payload: {
          comment: data_send.ht_comment,
          list: data_send.list,
          placement_id: placement_id,
        },
      });

      callBack(true, "");
    } catch (error) {
      console.log({ error });
      callBack(false, errorToText(error));
    }
  };
};

// * Update placement
export interface ChangePlacementStatus {
  type: ActionTypes.CHANGE_PLACEMENT_STATUS;
  payload: { id: string; dde_comment: string };
}

export type FT_ApprovePlacementList = (
  { placement_id, dde_comment }: { placement_id: string; dde_comment: string },
  callBack: (status: boolean, msg: string) => void
) => void;
export const FC_ApprovePlacementList = (
  { placement_id, dde_comment }: { placement_id: string; dde_comment: string },
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();
    try {
      let res = await axios.post<{ message: string }>(
        `${url}/placementapproval/approve/${placement_id}`,
        { dde_comment }
      );

      dispatch<ChangePlacementStatus>({
        type: ActionTypes.CHANGE_PLACEMENT_STATUS,
        payload: {
          dde_comment: dde_comment,
          id: placement_id,
        },
      });

      callBack(true, res.data.message);
    } catch (error) {
      console.log({ error });
      callBack(false, errorToText(error));
    }
  };
};

export const FC_RejectPlacementList = (
  { placement_id, dde_comment }: { placement_id: string; dde_comment: string },
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    setAxiosToken();
    try {
      let res = await axios.post<{ message: string }>(
        `${url}/placementapproval/reject/${placement_id}`,
        { dde_comment }
      );

      dispatch<ChangePlacementStatus>({
        type: ActionTypes.CHANGE_PLACEMENT_STATUS,
        payload: {
          dde_comment: dde_comment,
          id: placement_id,
        },
      });

      callBack(true, res.data.message);
    } catch (error) {
      console.log({ error });
      callBack(false, errorToText(error));
    }
  };
};

// report //////
export const FC_GetReportByDistrict = async (
  district: string,
  academic: string,
  month: string,
  callBack: (
    status: boolean,
    msg: string,
    data: API_GetPlacementBySchool[] | null
  ) => void
) => {
  callBack(true, "", null);
  try {
    setAxiosToken();
    const res = await axios.get<API_GetPlacementBySchool[]>(
      `${url}/placementapproval/monthlylist/district/${district}/${academic}/${month.toLocaleLowerCase()}`
    );
    // * save data
    const result = res.data.filter(
      (item) => Number(item.dde_approval_status) === 1
    );
    callBack(false, "", result);
  } catch (error) {
    callBack(
      false,
      errorToText(error, () => {}),
      null
    );
  }
};

