import axios from "axios";
import { Dispatch } from "redux";
import { APP_URL } from "../config/app.config";
import {
  APITerminationType,
  TerminationStatusEnum,
} from "../types/StaffTermination";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { ActionTypes } from "./types";

export type TerminationResponseType = {
  change_staff_status_id: string;
  decision: TerminationStatusEnum;
  decided_to_suspend_from: string;
  decided_to_suspend_to: string;
  decided_by_comment: string;
  mayor_letter: string;
};
/**
 * * ****************************** ACTIONS *****************************
 */
export interface GetAllTerminationAction {
  type: ActionTypes.GET_ALL_TERMINATION;
  payload: APITerminationType[] | null;
}

export interface UpdateTerminationAction {
  type: ActionTypes.UPDATE_TERMINATION;
  payload: APITerminationType[];
}

export const FC_GetAllTermination = (
  dataFor: "hd" | "hr",
  callBack: (
    status: boolean,
    data: APITerminationType[] | null,
    msg: string
  ) => void
) => {
  callBack(true, null, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<APITerminationType[]>(
        `${APP_URL}/changeteacherstatus/${dataFor}`
      );
      console.log("====================================");
      console.log({ getAllTermination: res });
      console.log("====================================");

      // * save data
      dispatch<GetAllTerminationAction>({
        type: ActionTypes.GET_ALL_TERMINATION,
        payload: res.data,
      });
      callBack(false, res.data, "");
    } catch (error) {
      console.log(error);
      callBack(false, null, errorToText(error));
      //   callBack(
      //     false,
      //     errorToText(error, () => {})
      //   );
    }
  };
};

export const FC_UpdateThisTermination = (
  thisTermination: TerminationResponseType,
  callBack: (status: boolean, msg: string) => void
) => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.post<APITerminationType>(
        `${APP_URL}/changeteacherstatus/hrchangeteacherstatus`,
        thisTermination
      );
      console.log("====================================");
      console.log({ FC_UpdateThisTermination: res, thisTermination });
      console.log("====================================");

      // -------------------------
      const resData = await axios.get<APITerminationType[]>(
        `${APP_URL}/changeteacherstatus/hr`
      );
      console.log("====================================");
      console.log({ getAllTermination: resData });
      console.log("====================================");

      // * save data
      dispatch<UpdateTerminationAction>({
        type: ActionTypes.UPDATE_TERMINATION,
        payload: resData.data,
      });
      callBack(true, "");
    } catch (error) {
      console.log(error);
      callBack(false, errorToText(error));
      //   callBack(
      //     false,
      //     errorToText(error, () => {})
      //   );
    }
  };
};
