import { Basic, Action, ActionTypes, SchoolHierarchyStore } from "../actions";
import { SystemUsersStore } from "../actions/system_users.action";

// default state
const defaultState: SystemUsersStore = {
  users: null,
  selected_page: 1,
};

export const systemUsersReducer = (
  state: SystemUsersStore = defaultState,
  action: Action
) => {
  switch (action.type) {
    case ActionTypes.GET_ALL_SYSTEM_USERS:
      return {
        ...state,
        users:
          state.users === null || action.payload.clean === true
            ? [
                {
                  total_users: action.payload.users.total_users,
                  page_number: action.payload.page_number,
                  users: action.payload.users.users,
                },
              ]
            : [
                ...state.users,
                {
                  total_users: action.payload.users.total_users,
                  page_number: action.payload.page_number,
                  users: action.payload.users.users,
                },
              ],
      };
    case ActionTypes.CLEAN_SYSTEM_USERS_LIST:
      return {
        ...state,
        users: null,
      };
    default:
      return state;
  }
};
