import { Auth, Action, ActionTypes } from "../actions";

// default state
const defaultState = {
  isAuthenticated: false,
  user: null,
  jwt: "",
  role: null,
  country: null,
  school: null,
  sector: null,
  stakeholder: null,
  save_code: false,
} as Auth;

export const authReducer = (state: Auth = defaultState, action: Action) => {
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.LOGIN_DETAILS:
      return {
        ...state,
        isAuthenticated: true,
        user: action.payload.user_info,
        role: action.payload.role,
        jwt: action.payload.jwt,
        school: action.payload.school,
        sector: action.payload.sector,
        country: action.payload.country,
        stakeholder: action.payload.stakeholder,
        district: action.payload.district,
        save_code: action.payload?.save_code
          ? action.payload?.save_code
          : false,
      } as Auth;
    case ActionTypes.SET_AUTH_CHANGE_PASS_CODE:
      return { ...state, save_code: action.payload } as Auth;
    default:
      return state;
  }
};
