export interface SchoolsPostsRequestsItem {
  academic_year_id: string;
  archive: string;
  archived_by: string | null;
  archived_date: string | null;
  created_by: string;
  created_date: string;
  dde_distribution_comment: string | null;
  dde_id_distribution: string | null;
  dde_id_request: string | null;
  dde_post_distribution: string | null;
  dde_post_request: string | null;
  dde_post_request_comment: string | null;
  district_code: string;
  existing_post: string;
  head_teacher_id: string;
  head_teacher_post_request: string;
  head_teacher_reason_id: string;
  position_code: string;
  position_name: string;
  post_request_id: string;
  qualification_id: string;
  qualification_name: string;
  school_code: string;
  school_name: string;
  updated_by: string | null;
  updated_date: string | null;
  reason_name: string;
}

export interface SchoolsPostsRequests {
  academic_distributed: SchoolsPostsRequestsItem[];
}

type DistrictRequestProps = {
  auth: Auth;
  basic: Basic;
};

type AlertMessage = {
  target: "main-error" | "main-success" | null;
  msg: string;
};

const _DistrictRequest: FC<DistrictRequestProps> = ({
  auth,
  basic,
}): ReactElement => {
  const [selectedRequestId, setSelectedRequestId] = useState<string>("");
  const [searchData, setSearchData] = useState<string>("");
  const [loading, setLoading] = useState<boolean>(false);
  const [alert, setAlert] = useState<AlertMessage>({
    target: null,
    msg: "",
  });
  const [schoolsPostsRequests, setSchoolsPostsRequests] = useState<
    SchoolsPostsRequests | null | undefined
  >(null);
  const [dataToSumbit, setDataToSumbit] = useState<DeeSubmtData>({
    post_request_id: "",
    district_code: "",
    dde_post_request: "",
    dde_post_request_comment: "",
  });

  const ClearDataToSubmit = (data: SchoolsPostsRequestsItem) => {
    setDataToSumbit({
      dde_post_request: "",
      dde_post_request_comment: "",
      district_code: data.district_code,
      post_request_id: data.post_request_id,
    });
  };

  const filteredData = (): SchoolsPostsRequestsItem[] => {
    if (schoolsPostsRequests) {
      let res = schoolsPostsRequests.academic_distributed;
      return search(res, searchData);
    } else {
      return [];
    }
  };

  const handleChange = (value: string) => {
    let output = {
      post_request_id: dataToSumbit.post_request_id,
      district_code: dataToSumbit.district_code,
      dde_post_request: value,
      dde_post_request_comment: dataToSumbit.dde_post_request_comment,
    };
    setDataToSumbit(output);
    // updatedDataHandler(output);
  };

  const handleChangeTextErea = (text: string) => {
    let output_text = {
      post_request_id: dataToSumbit.post_request_id,
      district_code: dataToSumbit.district_code,
      dde_post_request: dataToSumbit.dde_post_request,
      dde_post_request_comment: text,
    };
    setDataToSumbit(output_text);
    // updatedDataHandler(output_text);
  };

  const updatedDataHandler = (dataToSumbit: DeeSubmtData) => {
    let newUpdateData: SchoolsPostsRequestsItem[] = [];
    if (schoolsPostsRequests !== null && schoolsPostsRequests !== undefined) {
      for (let itm of schoolsPostsRequests.academic_distributed) {
        if (itm.post_request_id === dataToSumbit.post_request_id) {
          newUpdateData.push({
            academic_year_id: itm.academic_year_id,
            archive: itm.archive,
            archived_by: itm.archived_by,
            archived_date: itm.archived_date,
            created_by: itm.created_by,
            created_date: itm.created_date,
            dde_distribution_comment: itm.dde_distribution_comment,
            dde_id_distribution: itm.dde_id_distribution,
            dde_id_request: itm.dde_id_request,
            dde_post_distribution: itm.dde_post_distribution,
            dde_post_request: dataToSumbit.dde_post_request, //dde
            dde_post_request_comment: dataToSumbit.dde_post_request_comment, //dde
            district_code: itm.district_code,
            existing_post: itm.existing_post,
            head_teacher_id: itm.head_teacher_id,
            head_teacher_post_request: itm.head_teacher_post_request,
            head_teacher_reason_id: itm.head_teacher_reason_id,
            position_code: itm.position_code,
            position_name: itm.position_name,
            post_request_id: itm.post_request_id,
            qualification_id: itm.qualification_id,
            qualification_name: itm.qualification_name,
            school_code: itm.school_code,
            school_name: itm.school_name,
            updated_by: itm.updated_by,
            updated_date: itm.updated_date,
            reason_name: itm.reason_name,
          });
        } else {
          newUpdateData.push(itm);
        }
      }
    }
    schoolsPostsRequests !== null &&
      schoolsPostsRequests !== undefined &&
      schoolsPostsRequests.academic_distributed.map((element) => {
        if (element.post_request_id === dataToSumbit.post_request_id) {
          element.dde_post_request = dataToSumbit.dde_post_request;
          element.dde_post_request_comment =
            dataToSumbit.dde_post_request_comment;
        }
        return element;
      });
    if (newUpdateData !== null) {
      setSchoolsPostsRequests({
        academic_distributed: newUpdateData,
      });
    }
  };

  const submitHandler = async (teacher_requested: number) => {
    if (
      dataToSumbit.dde_post_request === "" ||
      dataToSumbit.dde_post_request === "0"
    ) {
      return setAlert({
        target: "main-error",
        msg: "Please fill number of requests",
      });
    } else if (dataToSumbit.post_request_id === "") {
      return setAlert({
        target: "main-error",
        msg: "Please choose a post request",
      });
    }
    setAlert({
      target: null,
      msg: "",
    });
    setLoading(true);
    try {
      setAxiosToken();
      let newDataSubmit = {
        post_request_id: dataToSumbit.post_request_id,
        dde_post_request: parseInt(dataToSumbit.dde_post_request),
        dde_post_request_comment: dataToSumbit.dde_post_request_comment,
      };
      const res = await axios.post(
        `${APP_URL}/districtdistribution/request`,
        newDataSubmit
      );
      updatedDataHandler(dataToSumbit);
      console.log("sub: ", res);
      setDataToSumbit({
        post_request_id: "",
        district_code: "",
        dde_post_request: "",
        dde_post_request_comment: "",
      });
      setSelectedRequestId("");
      cleanAlert();
      setAlert({
        target: "main-success",
        msg: "Request Submitted Successfully!",
      });
    } catch (error: any) {
      console.log("Err: ", { ...error });
      cleanAlert();
      setAlert({
        target: "main-error",
        msg: "Something went Wrong, Sending Request, Please try Again Later?",
      });
    }
    setLoading(false);
  };

  // const submitHandler = async (teacher_requested: number) => {
  //   if (
  //     dataToSumbit.dde_post_request === "" ||
  //     dataToSumbit.dde_post_request === "0"
  //   ) {
  //     return setAlert({
  //       target: "main-error",
  //       msg: "Please fill number of requests",
  //     });
  //   } else if (teacher_requested < parseInt(dataToSumbit.dde_post_request)) {
  //     return setAlert({
  //       target: "main-error",
  //       msg: `Number of request should not greater than ${teacher_requested}`,
  //     });
  //   } else if (dataToSumbit.post_request_id === "") {
  //     return setAlert({
  //       target: "main-error",
  //       msg: "Please choose a post request",
  //     });
  //   }
  //   setAlert({
  //     target: null,
  //     msg: "",
  //   });
  //   setLoading(true);
  //   try {
  //     setAxiosToken();
  //     let newDataSubmit = {
  //       post_request_id: dataToSumbit.post_request_id,
  //       dde_post_request: parseInt(dataToSumbit.dde_post_request),
  //       dde_post_request_comment: dataToSumbit.dde_post_request_comment,
  //     };
  //     const res = await axios.post(
  //       `${APP_URL}/districtdistribution/request`,
  //       newDataSubmit
  //     );
  //     updatedDataHandler(dataToSumbit);
  //     console.log("sub: ", res);
  //     setDataToSumbit({
  //       post_request_id: "",
  //       district_code: "",
  //       dde_post_request: "",
  //       dde_post_request_comment: "",
  //     });
  //     setSelectedRequestId("");
  //     cleanAlert();
  //     setAlert({
  //       target: "main-success",
  //       msg: "Request Submitted Successfully!",
  //     });
  //   } catch (error: any) {
  //     console.log("Err: ", { ...error });
  //     cleanAlert();
  //     setAlert({
  //       target: "main-error",
  //       msg: "Something went Wrong, Sending Request, Please try Again Later?",
  //     });
  //   }
  //   setLoading(false);
  // };

  // clear alert
  const cleanAlert = () => {
    setTimeout(() => {
      setAlert({
        target: null,
        msg: "",
      });
    }, 2000);
  };

  const gryDistrictRequests = async () => {
    setLoading(true);
    try {
      if (
        auth.district?.district_code &&
        basic.academic_calender?.academic_year_id
      ) {
        setAxiosToken();
        const res = await axios.get<SchoolsPostsRequests | null>(
          `${APP_URL}/districtdistribution/peracademic/${auth.district.district_code}/${basic.academic_calender.academic_year_id}`
        );
        console.log("Data: ", res.data);
        setSchoolsPostsRequests(res.data);
      }
    } catch (error: any) {
      console.log("bb err: ", { ...error });
      setSchoolsPostsRequests(undefined);
      cleanAlert();
      setAlert({
        target: "main-error",
        msg: "You Have Not Been Assigned to District",
      });
    }
    setLoading(false);
  };

  useEffect(() => {
    if (schoolsPostsRequests === null) {
      gryDistrictRequests();
    }
  });

  return (
    <div className="bg-white rounded p-3 m-2 mt-4 border min-h-full">
      <div className="flex flex-col mb-3">
        <span className="text-3xl font-bold">
          Schools posts requests approval
        </span>
        <span className="text-accent-900 -mt-1 font-bold">
          List of all head teacher schools requests
        </span>
      </div>
      {alert.target === "main-success" && (
        <Alert theme="success">{alert.msg}</Alert>
      )}
      {alert.target === "main-error" && (
        <Alert theme="danger">{alert.msg}</Alert>
      )}
      <div className="mt-3 h-full">
        <div className="mb-3">
          <input
            value={searchData}
            onChange={(e) => setSearchData(e.target.value)}
            type="text"
            className="px-4 py-2 rounded border-2 border-primary-900 w-full"
            placeholder="Search..."
          />
        </div>
        <table className="text-left min-w-full">
          <thead>
            <tr className="border border-b-2 text-sm">
              <th className="border px-1 text-center">#</th>
              <th className="border px-1">School</th>
              <th className="border px-1">Position Name</th>
              <th className="border px-1 text-center">Qualification Name</th>
              <th className="border px-1">Request Reason</th>
              <th className="border px-1 text-center">Existing teachers</th>
              <th className="border px-1 text-center">HT New Requests</th>
              <th className="border px-1 text-center">DDE Requests</th>
              <th className="border px-1">Comment</th>
            </tr>
          </thead>
          <tbody>
            {schoolsPostsRequests === null ||
            schoolsPostsRequests === undefined ||
            loading === true ? (
              <tr>
                <td
                  colSpan={11}
                  className="font-extrabold text-center p-2 text-yellow-900 text-xl"
                >
                  Loading, please wait...
                </td>
              </tr>
            ) : filteredData().length === 0 ? (
              <tr>
                <td colSpan={11} className="font-bold text-center p-2">
                  No Data Found
                </td>
              </tr>
            ) : (
              filteredData().map((element, index) => {
                return (
                  <Fragment key={index}>
                    <tr className="border rounded hover:bg-blue-100 group text-sm">
                      <td className="p-2 border text-center">{index + 1}</td>
                      <td className="p-2 border">{element.school_name}</td>
                      <td className="p-2 border">{element.position_name}</td>
                      <td className="p-2 border text-center">
                        {element.qualification_name}
                      </td>
                      <td className="p-2 border">{element.reason_name}</td>
                      <td className="p-2 border text-center">
                        {element.existing_post}
                      </td>
                      <td className="p-2 border text-center">
                        {element.head_teacher_post_request}
                      </td>

                      {selectedRequestId === element.post_request_id ? (
                        <td
                          className={` border z-50 w-12 relative text-center`}
                        >
                          <input
                            type="number"
                            className="w-full p-1 text-center border-4 rounded-t border-primary-800 font-bold"
                            value={
                              dataToSumbit.dde_post_request.length !== 0
                                ? dataToSumbit.dde_post_request
                                : schoolsPostsRequests.academic_distributed.find(
                                    (itm) =>
                                      itm.post_request_id === selectedRequestId
                                  ) === undefined
                                ? ""
                                : schoolsPostsRequests.academic_distributed.find(
                                    (itm) =>
                                      itm.post_request_id === selectedRequestId
                                  ) === null
                                ? ""
                                : schoolsPostsRequests.academic_distributed.find(
                                    (itm) =>
                                      itm.post_request_id === selectedRequestId
                                  )!.dde_post_request!
                            }
                            name="dde_post_request"
                            onChange={(e) => handleChange(e.target.value)}
                          />
                          <div className="animate__animated animate__zoomIn animate__faster absolute p-1 bg-primary-800 rounded-b -mt-0.5 rounded-tl  z-40 w-80 right-0 text-left">
                            <label
                              className="px-2 text-sm text-gray-300"
                              htmlFor="selected_comment"
                            >
                              Comment (optional)
                            </label>
                            <div className="px-2">
                              <textarea
                                id="selected_comment"
                                name="dde_post_request_comment"
                                className="w-full p-1 rounded"
                                value={
                                  dataToSumbit.dde_post_request_comment
                                    .length !== 0
                                    ? dataToSumbit.dde_post_request_comment
                                    : schoolsPostsRequests.academic_distributed.find(
                                        (itm) =>
                                          itm.post_request_id ===
                                          selectedRequestId
                                      ) === undefined
                                    ? ""
                                    : schoolsPostsRequests.academic_distributed.find(
                                        (itm) =>
                                          itm.post_request_id ===
                                          selectedRequestId
                                      ) === null
                                    ? ""
                                    : schoolsPostsRequests.academic_distributed.find(
                                        (itm) =>
                                          itm.post_request_id ===
                                          selectedRequestId
                                      )!.dde_post_request_comment!
                                }
                                onChange={(e) =>
                                  handleChangeTextErea(e.target.value)
                                }
                              ></textarea>
                            </div>
                            <div className="flex">
                              <button
                                onClick={(e) =>
                                  submitHandler(
                                    parseInt(element.head_teacher_post_request)
                                  )
                                }
                                className="p-1 px-4 rounded text-white bg-green-400 hover:bg-green-500 mx-2 my-1"
                              >
                                Send request
                              </button>
                              <button
                                onClick={() => {
                                  setSelectedRequestId("");
                                  setDataToSumbit({
                                    post_request_id: "",
                                    district_code: "",
                                    dde_post_request: "",
                                    dde_post_request_comment: "",
                                  });
                                }}
                                className="p-1 px-2 rounded text-white bg-red-600 hover:bg-red-600  my-1"
                              >
                                Cancel
                              </button>
                            </div>
                          </div>
                        </td>
                      ) : (
                        <td
                          className={`p-2 text-center border ${
                            element.dde_post_request === null
                              ? "border-yellow-400 bg-yellow-100 hover:bg-yellow-300"
                              : "border-blue-400 group-hover:border-primary-800 bg-blue-100 hover:bg-blue-300 group-hover:bg-primary-800 group-hover:text-white"
                          }  w-12 font-bold cursor-pointer`}
                          onClick={() => {
                            setDataToSumbit({
                              post_request_id: element.post_request_id,
                              district_code: element.district_code,
                              dde_post_request:
                                element.dde_post_request === null
                                  ? ""
                                  : element.dde_post_request,
                              dde_post_request_comment:
                                element.dde_post_request_comment === null
                                  ? ""
                                  : element.dde_post_request_comment,
                            });
                            setSelectedRequestId(element.post_request_id);
                            // ClearDataToSubmit(element);
                          }}
                        >
                          {element.dde_post_request === null ? (
                            <span>Add</span>
                          ) : (
                            element.dde_post_request
                          )}
                        </td>
                      )}
                      <td className="p-2 border">
                        <div className="truncate" style={{ maxWidth: "100px" }}>
                          {element.dde_post_request_comment}
                        </div>
                      </td>
                    </tr>
                  </Fragment>
                );
              })
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

const DistrictRequest = connect(mapStateToProps, {})(_DistrictRequest);

export default DistrictRequest;
