import { Action, ActionTypes, Qualifications } from "../actions";
import { TransferReasonsType } from "../actions/transfer.action";
import { RequestedToLeave, RequestedTransfer } from "../types/TransferTypes";

export type TransferReducerType = {
  allTransfers: RequestedToLeave[] | null;
  myTransfers: RequestedTransfer[] | null;
  transferReasons: TransferReasonsType[] | null;
  qualifications: Qualifications[] | null;
};
// default state
const defaultState: TransferReducerType = {
  allTransfers: null,
  myTransfers: null,
  transferReasons: null,
  qualifications: null,
};

export const transferReducer = (
  state: TransferReducerType = defaultState,
  action: Action
) => {
  let index: number = -1;
  let allTransferCopy: RequestedToLeave[];
  switch (action.type) {
    case ActionTypes.LOGOUT:
      return defaultState;
    case ActionTypes.GET_ALL_TRANSFER:
      return {
        ...state,
        allTransfers: action.payload,
      } as TransferReducerType;
    case ActionTypes.GET_MY_TRANSFER:
      return {
        ...state,
        myTransfers: action.payload,
      } as TransferReducerType;
    case ActionTypes.GET_TRANSFER_REASON:
      return {
        ...state,
        transferReasons: action.payload,
      } as TransferReducerType;
    case ActionTypes.GET_QUALIFICATION:
      return {
        ...state,
        qualifications: action.payload,
      } as TransferReducerType;
    case ActionTypes.RESPOND_TO_LEAVE:
      index = state.allTransfers
        ? state.allTransfers.findIndex(
            (item) =>
              item.teacher_transfer_id.toString() ===
              action.payload.teacher_transfer_id
          )
        : -1;
      if (index >= 0) {
        allTransferCopy = [...state.allTransfers!];
        allTransferCopy[index].outgoing_dde_comment =
          action.payload.outgoing_dde_comment;
        allTransferCopy[index].mayor_letter = action.payload.mayor_letter;
        allTransferCopy[index].outgoing_status = action.payload.outgoing_status;
        return {
          ...state,
          allTransfers: allTransferCopy,
        } as TransferReducerType;
      } else return state;
    case ActionTypes.RESPOND_TO_TRANSFER:
      index = state.allTransfers
        ? state.allTransfers.findIndex(
            (item) =>
              item.teacher_transfer_id.toString() ===
              action.payload.teacher_transfer_id
          )
        : -1;
      if (index >= 0) {
        allTransferCopy = [...state.allTransfers!];
        allTransferCopy[index].requested_comment =
          action.payload.requested_comment;
        allTransferCopy[index].requested_status =
          action.payload.requested_status;
        allTransferCopy[index].approved_school_id =
          action.payload.approved_school_id;
        return {
          ...state,
          allTransfers: allTransferCopy,
        } as TransferReducerType;
      } else return state;
    // case ActionTypes.UPDATE_TERMINATION:
    //   return {
    //     ...state,
    //     allTransfers: action.payload,
    //   } as TransferReducerType;
    // case ActionTypes.UPDATE_TERMINATION:
    //   if (state.allTermination) {
    //     const index = state.allTermination.findIndex(
    //       (item) =>
    //         item.change_staff_status_id ===
    //         action.payload.change_staff_status_id
    //     );
    //     if (index >= 0) {
    //       let terminations = [...state.allTermination];
    //       terminations[index].decided_by_date = new Date().toDateString();
    //       terminations[index].decided_by_comment =
    //         action.payload.decided_by_comment;
    //       terminations[index].decided_to_suspend_to =
    //         action.payload.decided_to_suspend_to;
    //       terminations[index].decided_to_suspend_from =
    //         action.payload.decided_to_suspend_from;
    //       return {
    //         ...state,
    //         allTermination: terminations,
    //       } as TransferReducerType;
    //     }
    //   }
    //   return state;
    default:
      return state;
  }
};
