// import
export const errorToText = (error: any, NoInternet?: () => void): string => {
  // console.error({ ...error });
  // if (error?.request?.response === undefined) {
  //   if (!error.request) return "Error";
  //   let error_res = JSON.parse(error.request.response);
  //   if (error_res) {
  //     return error_res.message;
  //   } else {
  //     return "Something went wrong, please try again later";
  //   }
  // } else {
  //   if (error?.request.status !== 0) {
  //     return error?.request.statusText;
  //   } else {
  //     if (NoInternet) NoInternet();
  //     return "Network Error";
  //   }
  // }

  console.error({ ...error });

  if (error?.response !== undefined) {
    if (error?.response?.data?.message !== undefined) {
      return error?.response?.data?.message;
    } else {
      return "Something went wrong, please try again later";
    }
  } else {
    if (error.request === undefined || error.request.status === undefined) {
      return "Something went wrong, please try again later";
    } else if (error?.request.status !== 0) {
      return error?.request.statusText;
    } else {
      if (NoInternet) NoInternet();
      return "Please check your internet connection";
    }
  }
};
