import React, { FC, ReactElement, useState, useEffect, Fragment } from "react";
import axios from "axios";
import Container from "../../components/ContainerComponents/Container";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import { Auth } from "../../actions/auth.action";
import { Link } from "react-router-dom";
import { AcademicYearDetailsInterface } from "../../types/AcademicTypes";
import { setAxiosToken } from "../../utils/AxiosToken";
import { APP_URL } from "../../config/app.config";

type AcademicYearDetailsProps = {
  auth: Auth;
};

const _AcademicYearDetails: FC<AcademicYearDetailsProps> = (): ReactElement => {
  const now = new Date().getTime();
  const [academicDetailsData, setAcademicDetailsData] = useState<
    AcademicYearDetailsInterface[]
  >([]);

  useEffect(() => {
    setAxiosToken();
    axios
      .get(`${APP_URL}/academiccalendars`)
      .then((response) => response.data)
      .then((data) => setAcademicDetailsData(data))
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <Container title="ACADEMI YEAR CALENDAR" subTitle="Calendar">
      <div className="bg-white shadow-lg rounded-md p-5">
        <Link to={"/academic/new"}>
          <button className=" text-center bg-accent-800 hover:bg-accent-900 rounded-md p-2 m-2">
            Add New Calendar
          </button>
        </Link>
        <table className="w-full text-left">
          <thead>
            <tr>
              <th>Name</th>
              <th>Start Date</th>
              <th>End Date</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            {academicDetailsData.map((element, index) => {
              const academicYearEnd = new Date(
                element.academic_year_end
              ).getTime();
              const transferRequestEnd = new Date(
                element.transfer_request_end
              ).getTime();
              const internalTransferAssessmentEnd = new Date(
                element.internal_transfer_assessment_end
              ).getTime();
              const externalTransferAssessmentEnd = new Date(
                element.external_transfer_assessment_end
              ).getTime();
              const postRequestEnd = new Date(
                element.post_request_end
              ).getTime();
              const teacherRecruitmentEnd = new Date(
                element.teacher_recruitment_end
              ).getTime();
              return (
                <Fragment key={index}>
                  <tr>
                    <td className="p-4" colSpan={4}></td>
                  </tr>
                  <tr key={element.academic_year_name}>
                    <th
                      className="p-2 border text-center bg-blue-200"
                      colSpan={4}
                    >
                      {element.academic_year_name}
                    </th>
                  </tr>
                  <tr
                    key={"Acadmic"}
                    className="border rounded hover:bg-blue-100"
                  >
                    <th className="p-2 border capitalize">{"Acadmic"}</th>
                    <td className="p-2 border">
                      {element.academic_year_start}
                    </td>
                    <td className="p-2 border">{element.academic_year_end}</td>
                    <td className="p-2 border">
                      {academicYearEnd > now ? "Active" : "Ended"}
                    </td>
                  </tr>
                  <tr
                    key={"Request"}
                    className="border rounded hover:bg-blue-100"
                  >
                    <th className="p-2 border capitalize">{"Request Post"}</th>
                    <td className="p-2 border">{element.post_request_start}</td>
                    <td className="p-2 border">{element.post_request_end}</td>
                    <td className="p-2 border">
                      {postRequestEnd > now ? "Active" : "Ended"}
                    </td>
                  </tr>
                  <tr
                    key={"transfer_request"}
                    className="border rounded hover:bg-blue-100"
                  >
                    <th className="p-2 border capitalize">
                      {"transfer request"}
                    </th>
                    <td className="p-2 border">
                      {element.transfer_request_start}
                    </td>
                    <td className="p-2 border">
                      {element.transfer_request_end}
                    </td>
                    <td className="p-2 border">
                      {transferRequestEnd > now ? "Active" : "Ended"}
                    </td>
                  </tr>
                  <tr
                    key={"internal_assessment"}
                    className="border rounded hover:bg-blue-100"
                  >
                    <th className="p-2 border capitalize">
                      {"internal assessment"}
                    </th>
                    <td className="p-2 border">
                      {element.internal_transfer_assessment_start}
                    </td>
                    <td className="p-2 border">
                      {element.internal_transfer_assessment_end}
                    </td>
                    <td className="p-2 border">
                      {internalTransferAssessmentEnd > now ? "Active" : "Ended"}
                    </td>
                  </tr>
                  <tr
                    key={"external_assessment"}
                    className="border rounded hover:bg-blue-100"
                  >
                    <th className="p-2 border capitalize">
                      {"external assessment"}
                    </th>
                    <td className="p-2 border">
                      {element.external_transfer_assessment_start}
                    </td>
                    <td className="p-2 border">
                      {element.external_transfer_assessment_end}
                    </td>
                    <td className="p-2 border">
                      {externalTransferAssessmentEnd > now ? "Active" : "Ended"}
                    </td>
                  </tr>
                  <tr
                    key={"teacher_recruitment_start"}
                    className="border rounded hover:bg-blue-100"
                  >
                    <th className="p-2 border capitalize">
                      {"teacher recruitment"}
                    </th>
                    <td className="p-2 border">
                      {element.teacher_recruitment_start}
                    </td>
                    <td className="p-2 border">
                      {element.teacher_recruitment_end}
                    </td>
                    <td className="p-2 border">
                      {teacherRecruitmentEnd > now ? "Active" : "Ended"}
                    </td>
                  </tr>
                </Fragment>
              );
            })}
          </tbody>
        </table>
      </div>
    </Container>
  );
};

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

const AcademicYearDetails = connect(mapStateToProps, {})(_AcademicYearDetails);

export default AcademicYearDetails;
